import React, { useState, useEffect } from 'react';
import Image from 'next/image';
import { Menu, X } from 'lucide-react';
import Button from './ui/button';
import '../styles/header.css';

const Header = ({ onShowForm }) => {
  const [isScrolled, setIsScrolled] = useState(false);
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);

  useEffect(() => {
    const handleScroll = () => {
      setIsScrolled(window.scrollY > 0);
    };

    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  const closeMenu = () => {
    setIsMobileMenuOpen(false);
  };

  const handleLinkClick = (e) => {
    const href = e.currentTarget.getAttribute('href');
    if (href.startsWith('#')) {
      e.preventDefault();
      const element = document.querySelector(href);
      if (element) {
        const headerOffset = 80;
        const elementPosition = element.getBoundingClientRect().top;
        const offsetPosition = elementPosition + window.pageYOffset - headerOffset;

        window.scrollTo({
          top: offsetPosition,
          behavior: 'smooth'
        });
      }
      closeMenu();
    }
  };

  return (
    <>
      {/* Franja clara detrás del header cuando se hace scroll */}
      <div className={`header-bg ${isScrolled ? 'header-bg-scrolled' : ''} z-40`}></div>

      <header className="fixed top-0 left-0 right-0 z-50">
        <div className="max-w-7xl mx-auto px-4 sm:px-6">
          <div className="header-content flex justify-between items-center py-4 md:space-x-10">
            {/* Logo */}
            <div className="logo-container">
              <Image
                src="/brand/Logo horizontal.png"
                alt="Abogados Online Ecuador"
                width={180}
                height={40}
                className="h-12 w-auto"
                priority
              />
            </div>

            {/* Mobile menu button */}
            <div className="md:hidden">
              <button
                onClick={() => setIsMobileMenuOpen(!isMobileMenuOpen)}
                className={`mobile-menu-button ${
                  isScrolled ? 'mobile-menu-button-scrolled' : 'mobile-menu-button-transparent'
                }`}
                aria-label="Menu principal"
              >
                {isMobileMenuOpen ? (
                  <X className="h-6 w-6" />
                ) : (
                  <Menu className="h-6 w-6" />
                )}
              </button>
            </div>

            {/* Desktop navigation */}
            <nav className="hidden md:flex space-x-8 items-center">
              {[
                { href: '#servicios', label: 'Servicios' },
                { href: '#calculadora', label: 'Calculadora' },
                { href: '#contacto', label: 'Contacto' }
              ].map(({ href, label }) => (
                <a
                  key={href}
                  href={href}
                  onClick={handleLinkClick}
                  className={`nav-link ${
                    isScrolled ? 'nav-link-scrolled' : 'nav-link-transparent'
                  }`}
                >
                  {label}
                </a>
              ))}
              <Button
                onClick={onShowForm}
                className="cta-button"
              >
                Agendar Cita
              </Button>
            </nav>
          </div>
        </div>

        {/* Mobile menu */}
        <div
          className={`mobile-menu-container md:hidden transition-all duration-300 ease-in-out ${
            isMobileMenuOpen ? 'mobile-menu-enter-active' : 'mobile-menu-exit'
          }`}
        >
          <div className="px-4 pt-2 pb-3 space-y-1">
            {[
              { href: '#servicios', label: 'Servicios' },
              { href: '#calculadora', label: 'Calculadora' },
              { href: '#contacto', label: 'Contacto' }
            ].map(({ href, label }) => (
              <a
                key={href}
                href={href}
                onClick={handleLinkClick}
                className="mobile-menu-item"
              >
                {label}
              </a>
            ))}
            <Button
              onClick={() => {
                onShowForm();
                closeMenu();
              }}
              className="w-full cta-button mt-4"
            >
              Agendar Cita
            </Button>
          </div>
        </div>
      </header>
    </>
  );
};

export default Header;
