import React, { useState } from 'react';
import Button from '../components/ui/button';

const HeroSection = ({ onShowForm }) => {
  const [videoError, setVideoError] = useState(false);

  const scrollToCalculator = () => {
    const calculatorSection = document.querySelector('#calculadora');
    if (calculatorSection) {
      const headerOffset = 80;
      const elementPosition = calculatorSection.getBoundingClientRect().top;
      const offsetPosition = elementPosition + window.pageYOffset - headerOffset;

      window.scrollTo({
        top: offsetPosition,
        behavior: 'smooth'
      });
    }
  };

  return (
    <div className="relative min-h-[calc(100vh-5rem)] overflow-hidden bg-[#0A1E4C]">
      {/* Video de fondo */}
      {!videoError && (
        <div className="absolute inset-0 z-0">
          <video
            autoPlay
            loop
            muted
            playsInline
            className="absolute top-1/2 left-1/2 min-w-full min-h-full w-auto h-auto transform -translate-x-1/2 -translate-y-1/2 object-cover opacity-70"
            onError={() => setVideoError(true)}
          >
            <source src="/videos/hero-background.mp4" type="video/mp4" />
          </video>
        </div>
      )}
      
      {/* Overlay con gradiente */}
      <div className="absolute inset-0 bg-gradient-to-br from-[#0A1E4C]/30 via-[#0A1E4C]/20 to-transparent"></div>
      
      {/* Contenido */}
      <div className="relative z-20 max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-24 min-h-[calc(100vh-5rem)] flex items-center">
        <div className="text-center max-w-3xl mx-auto text-white">
          <h1 className="text-5xl md:text-6xl font-bold tracking-tight mb-8 leading-tight">
            Trámites Notariales
            <span className="block text-[#BF9B30] mt-2 drop-shadow-lg">Simplificados</span>
          </h1>
          
          <p className="text-xl md:text-2xl text-gray-100 mb-12 leading-relaxed">
            Ahorra tiempo y dinero con nuestra calculadora de costos en tiempo real y asesoría profesional personalizada
          </p>
          
          <div className="flex flex-col sm:flex-row justify-center gap-6">
            <Button 
              onClick={scrollToCalculator}
              className="bg-blue-600 hover:bg-blue-700 text-lg px-8 py-4 text-white font-semibold shadow-lg hover:shadow-xl transform hover:-translate-y-0.5 transition-all duration-200"
            >
              Calcular Costos Notariales
            </Button>
            <Button 
              onClick={onShowForm}
              className="bg-white text-blue-900 hover:bg-gray-100 text-lg px-8 py-4 font-semibold shadow-lg hover:shadow-xl transform hover:-translate-y-0.5 transition-all duration-200"
            >
              Consulta Gratuita
            </Button>
          </div>

          <div className="mt-20 grid grid-cols-1 sm:grid-cols-3 gap-8 text-center">
            <div className="bg-black/20 backdrop-blur-sm rounded-lg p-6 transform hover:-translate-y-1 transition-all duration-200 border border-white/10 hover:border-[#BF9B30]/30">
              <div className="text-4xl font-bold text-[#BF9B30] mb-2">10+</div>
              <div className="text-gray-100 font-medium">Años de Experiencia Legal</div>
            </div>
            <div className="bg-black/20 backdrop-blur-sm rounded-lg p-6 transform hover:-translate-y-1 transition-all duration-200 border border-white/10 hover:border-[#BF9B30]/30">
              <div className="text-4xl font-bold text-[#BF9B30] mb-2">30%</div>
              <div className="text-gray-100 font-medium">Ahorro Promedio</div>
            </div>
            <div className="bg-black/20 backdrop-blur-sm rounded-lg p-6 transform hover:-translate-y-1 transition-all duration-200 border border-white/10 hover:border-[#BF9B30]/30">
              <div className="text-4xl font-bold text-[#BF9B30] mb-2">24/7</div>
              <div className="text-gray-100 font-medium">Soporte Online</div>
            </div>
          </div>
        </div>
      </div>

      {/* Degradado inferior */}
      <div className="absolute bottom-0 left-0 right-0 h-32 bg-gradient-to-t from-[#0A1E4C] to-transparent"></div>
    </div>
  );
};

export default HeroSection;
